body {
  margin: 0;
  
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  font-family: "VT323";
  font-size: 40px;
}

@font-face {
  font-family: "VT323";
  src: url("./../../public/VT323-Regular.ttf");
}

button {
  border-radius: 10px;
  border: none;
  background: linear-gradient(45deg, black, black, darkblue);
  color: aquamarine;
}

button:active {
  background: linear-gradient(45deg, pink, slategray, blue);
  color: black;
}

.container {
  background: linear-gradient(black, #050f1e);
  color: whitesmoke;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
}

.game_field {
  display: flex;
  justify-content: center;
}

.joystick_wrap {
  /* background-color: red; */
  display: flex;
  justify-content: center;
  margin-top: 20%;
  margin-bottom: 20%;
}

#game_cnv {
  background-color: black;
  /* border: 3px solid crimson; */
  /* border-radius: 10px; */
  /* margin: 0 auto; */
  /* margin-bottom: 20%; */
  /* margin-top: 5%; */
}

.main_menu_container {
  /* align-items: center; */
}

@keyframes logo_animation {
  0% {
    color: aqua;
  }
  50% {
    color: lime;
  }
  100% {
    color: aqua;
  }
}

#main_manu_logo {
  font-size: 7vh;
  background: linear-gradient(crimson, #050f1e);
  color: aqua;
  font-style: italic;
  /* box-shadow: */
  border-radius: 30px;
  padding-inline: 20px;

  margin-top: 20%;

  -webkit-animation: logo_animation 5s linear infinite;
  -moz-animation:    logo_animation 5s linear infinite;
  animation:         logo_animation 5s linear infinite;
}

.main_menu_items_container {
  display: flex;
  flex-direction: column;
}

.main_menu_items_container > button {
  margin-top: 20%;
}